@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.9.1/font/bootstrap-icons.css");

*,
*>* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  height: 100vh;
}

.App {
  display: grid;
  grid-template-rows: minmax(0, 0.5fr) minmax(0, 5fr) minmax(0, 0.3fr);
  height: 100%;
  min-height: 0;
  min-width: 0;
}